import { Form, Divider, Input, InputNumber, Button, Upload, message, Select, Checkbox } from "antd";
import "./index.css";
import { useState } from "react";
import { API_URL } from "../config/constants";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const { Option } = Select;

function UploadPage() {
    const [imageUrl, setImageUrl] = useState(null);
    const [isBest, setIsBest] = useState(false);
    const navigate = useNavigate();

    const categories = [
      { label: " ", value: "snack" },
      { label: "", value: "japan" },
      { label: "", value: "china" },
      { label: "", value: "asia" },
      { label: "", value: "europe" },
      { label: "", value: "usa" },
      { label: "/", value: "ramen" },
      { label: "/", value: "drink" }
    ];

    const onSubmit = (values) => {
        if (!imageUrl) {
            message.error("  ");
            return;
        }

        axios
            .post(`${API_URL}/products`, {
                name: values.name,
                description: values.description,
                seller: values.seller,
                price: parseInt(values.price),
                quantity: parseInt(values.quantity),
                imageUrl: imageUrl,
                category: values.category,
                isBest: isBest
            })
            .then(() => {
                message.success("  .");
                navigate(`/`);
            })
            .catch((error) => {
                console.error(error);
                message.error(` . ${error.message}`);
            });
    };

    const onChangeImage = (info) => {
        if (info.file.status === "uploading") {
            return;
        }
        if (info.file.status === "done") {
            const response = info.file.response;
            setImageUrl(response.imageUrl);
        }
    };

    return (
        <div id="upload-container">
            <Form name=" " onFinish={onSubmit}>
                <Form.Item
                    name="upload"
                    label={<div className="upload-label"> </div>}
                >
                    <Upload
                        name="image"
                        action={`${API_URL}/image`}
                        listType="picture"
                        showUploadList={false}
                        onChange={onChangeImage}
                    >
                        {imageUrl ? (
                            <img id="upload-img" src={`${API_URL}/${imageUrl}`} alt="uploaded" />
                        ) : (
                            <div id="upload-img-placeholder">
                                <img src="/images/icons/camera.png" alt="placeholder" />
                                <span> .</span>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Divider />
                <Form.Item
                    name="category"
                    label={<div className="upload-label"></div>}
                    rules={[{ required: true, message: " " }]}
                >
                    <Select placeholder=" " size="large">
                        {categories.map(category => (
                            <Option key={category.value} value={category.value}>
                                {category.label}
                            </Option>
                        ))}
                    </Select>
                </Form.Item>
                <Divider />
                <Form.Item
                    label={<div className="upload-label"> </div>}
                    name="seller"
                    rules={[{ required: true, message: "  " }]}
                >
                    <Input
                        className="upload-name"
                        size="large"
                        placeholder=" "
                    />
                </Form.Item>
                <Divider />
                <Form.Item
                    name="name"
                    label={<div className="upload-label"> </div>}
                    rules={[{ required: true, message: "  " }]}
                >
                    <Input
                        className="upload-name"
                        size="large"
                        placeholder="  "
                    />
                </Form.Item>
                <Divider />
                <Form.Item
                    name="price"
                    label={<div className="upload-label"> </div>}
                    rules={[{ required: true, message: "  " }]}
                >
                    <InputNumber 
                        defaultValue={0} 
                        className="upload-price" 
                        size="large"
                        min={0}
                    />
                </Form.Item>
                <Divider />
                <Form.Item
                    name="quantity"
                    label={<div className="upload-label"> </div>}
                    rules={[{ required: true, message: "  " }]}
                >
                    <InputNumber 
                        defaultValue={1} 
                        className="upload-quantity" 
                        size="large"
                        min={1}
                        placeholder="  "
                    />
                </Form.Item>
                <Divider />
                <Form.Item
                    name="description"
                    label={<div className="upload-label"> </div>}
                    rules={[{ required: true, message: "  " }]}
                >
                    <Input.TextArea
                        size="large"
                        id="product-description"
                        showCount
                        maxLength={300}
                        placeholder="  ."
                    />
                </Form.Item>
                <Divider />
                <Form.Item name="isBest" valuePropName="checked">
                    <Checkbox onChange={(e) => setIsBest(e.target.checked)}>
                         
                    </Checkbox>
                </Form.Item>
                <Form.Item>
                    <Button id="submit-button" size="large" htmlType="submit">
                         
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
}

export default UploadPage;
