import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button, Input, Form, message } from "antd";
import "./index.css";

function LoginPage({ setIsLoggedIn }) {
  const [isLoginView, setIsLoginView] = useState(true);
  const [form] = Form.useForm();
  const navigate = useNavigate();

  const onFinishLogin = (values) => {
    const userInfo = localStorage.getItem(values.id);
    
    if (!userInfo) {
      message.error("  .");
      return;
    }

    const user = JSON.parse(userInfo);
    if (user.password !== values.password) {
      message.error("  .");
      return;
    }

    //  
    localStorage.setItem("userToken", values.id);
    localStorage.setItem("currentUser", JSON.stringify(user));
    setIsLoggedIn(true);
    message.success(" !");
    navigate("/");
  };

  const onFinishRegister = (values) => {
    const existingUser = localStorage.getItem(values.id);
    if (existingUser) {
      message.error("  .");
      return;
    }

    if (values.password !== values.confirmPassword) {
      message.error("  .");
      return;
    }

    const userInfo = {
      nickname: values.nickname,
      id: values.id,
      password: values.password,
      role: "user" //   'user' 
    };
    
    localStorage.setItem(values.id, JSON.stringify(userInfo));
    message.success(" .");
    form.resetFields();
    setIsLoginView(true);
  };

  return (
    <div className="login-container">
      <div className="login-card">
        {isLoginView ? (
          <>
            <h2></h2>
            <Form form={form} onFinish={onFinishLogin}>
              <Form.Item
                name="id"
                rules={[{ required: true, message: " " }]}
              >
                <Input placeholder="" />
              </Form.Item>
              <Form.Item
                name="password"
                rules={[{ required: true, message: " " }]}
              >
                <Input.Password placeholder="" />
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-button">
                  
                </Button>
              </Form.Item>
            </Form>
            <div className="switch-form">
               ?{" "}
              <Button type="link" onClick={() => setIsLoginView(false)}>
                
              </Button>
            </div>
          </>
        ) : (
          <>
            <h2></h2>
            <Form form={form} onFinish={onFinishRegister}>
              <Form.Item
                name="nickname"
                rules={[{ required: true, message: " " }]}
              >
                <Input placeholder="" />
              </Form.Item>
              <Form.Item
                name="id"
                rules={[{ required: true, message: " " }]}
              >
                <Input placeholder="" />
              </Form.Item>
              <Form.Item
                name="password"
                rules={[{ required: true, message: " " }]}
              >
                <Input.Password placeholder="" />
              </Form.Item>
              <Form.Item
                name="confirmPassword"
                rules={[
                  { required: true, message: "  " },
                ]}
              >
                <Input.Password placeholder=" " />
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-button">
                  
                </Button>
              </Form.Item>
            </Form>
            <div className="switch-form">
                ?{" "}
              <Button type="link" onClick={() => setIsLoginView(true)}>
                
              </Button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default LoginPage;
