import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { message, Modal, Form, Input, Rate, Button } from "antd";
import "./MyPage.css";
import dayjs from "dayjs";
import { API_URL } from "../config/constants";
import axios from "axios";

function MyPage({ userNickname, isLoggedIn }) {
  const [cartItems, setCartItems] = useState([]);
  const [purchasedItems, setPurchasedItems] = useState([]);
  const [address, setAddress] = useState("");
  const [zipcode, setZipcode] = useState("");
  const [savedAddress, setSavedAddress] = useState(null);
  const [isEditingAddress, setIsEditingAddress] = useState(false);
  const [isReviewModalVisible, setIsReviewModalVisible] = useState(false);
  const [selectedItemForReview, setSelectedItemForReview] = useState(null);
  const [form] = Form.useForm();
  const navigate = useNavigate();
  
  useEffect(() => {
    if (!isLoggedIn) {
      message.warning(" .");
      navigate("/login");
      return;
    }

    const userData = JSON.parse(localStorage.getItem(userNickname)) || {};
    const {
      address: storedAddress,
      zipcode: storedZipcode,
      cartItems: storedCartItems,
      purchasedItems: storedPurchasedItems
    } = userData;

    if (storedAddress && storedZipcode) {
      setSavedAddress({ address: storedAddress, zipcode: storedZipcode });
    }
    setCartItems(storedCartItems || []);
    setPurchasedItems(storedPurchasedItems || []);
  }, [userNickname, isLoggedIn, navigate]);

  const handleSaveAddress = () => {
    if (address && zipcode) {
      setSavedAddress({ address, zipcode });
      const updatedUserData = {
        ...JSON.parse(localStorage.getItem(userNickname)),
        address,
        zipcode,
      };
      localStorage.setItem(userNickname, JSON.stringify(updatedUserData));
      message.success(" !");
      setIsEditingAddress(false);
    } else {
      message.error("  .");
    }
  };

  const handleItemClick = (itemId) => {
    navigate(`/products/${itemId}`);
  };

  const handleRemoveItem = (itemId) => {
    const updatedCartItems = cartItems.filter((item) => item.id !== itemId);
    const userData = JSON.parse(localStorage.getItem(userNickname)) || {};
    const updatedUserData = {
      ...userData,
      cartItems: updatedCartItems,
    };
    localStorage.setItem(userNickname, JSON.stringify(updatedUserData));
    setCartItems(updatedCartItems);
    message.success("  .");
  };

  const handleReviewClick = (item) => {
    setSelectedItemForReview(item);
    setIsReviewModalVisible(true);
  };

  const handleReviewSubmit = async (values) => {
    try {
      const response = await axios.post(`${API_URL}/products/${selectedItemForReview.id}/reviews`, {
        ...values,
        userId: userNickname,
      });
      message.success(" .");
      setIsReviewModalVisible(false);
      form.resetFields();
      
      const userData = JSON.parse(localStorage.getItem(userNickname)) || {};
      const userReviews = userData.reviews || [];
      const newReview = {
        ...response.data.review,
        productId: selectedItemForReview.id
      };
      userData.reviews = [...userReviews, newReview];
      localStorage.setItem(userNickname, JSON.stringify(userData));
    } catch (error) {
      message.error("    .");
    }
  };

  return (
    <div className="my-page">
      <header className="my-page-header">
        <h1></h1>
      </header>

      <section className="profile-section">
        <div className="profile-info">
          <img src="/images/icons/avatar.png" alt=" " className="profile-avatar" />
          <div>
            <h2>{userNickname}</h2>
          </div>
        </div>
      </section>

      <section className="address-section">
        <h3 style={{ fontWeight: 'bold', fontSize: '18px' }}> </h3>
        {savedAddress ? (
          <>
            <p>: {savedAddress.address}</p>
            <p>: {savedAddress.zipcode}</p>
          </>
        ) : (
          <p>  .</p>
        )}
        {!isEditingAddress && (
          <button onClick={() => setIsEditingAddress(true)}> </button>
        )}
        {isEditingAddress && (
          <div className="address-form">
            <input
              type="text"
              placeholder=" "
              value={address}
              onChange={(e) => setAddress(e.target.value)}
            />
            <input
              type="text"
              placeholder=" "
              value={zipcode}
              onChange={(e) => setZipcode(e.target.value)}
            />
            <button onClick={handleSaveAddress}></button>
            <button onClick={() => setIsEditingAddress(false)}></button>
          </div>
        )}
      </section>

      <section className="cart-section">
        <h3></h3>
        {cartItems.length > 0 ? (
          <div className="cart-grid">
            {cartItems.map((item, index) => (
              <div
                key={index}
                className="cart-item-card"
                onClick={() => handleItemClick(item.id)}
              >
                <div id="image-box">
                  <img src={`${API_URL}/${item.imageUrl}`} alt=" " />
                </div>
                <div className="cart-item-details">
                  <p className="cart-item-name">{item.name}</p>
                  <p className="cart-item-price">{item.price}</p>
                  <p className="cart-item-seller">
                    <img src="/images/icons/avatar.png" alt=" " className="seller-icon" />
                    {item.seller}
                  </p>
                  <p className="cart-item-createdAt">{dayjs(item.createdAt).fromNow()}</p>
                  <button
                  className="remove-button"
                  onClick={(e) => {
                    e.stopPropagation();
                    handleRemoveItem(item.id);
                  }}
                >
                  
                </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <p>  .</p>
        )}
      </section>

      <section className="purchased-section">
        <h3> </h3>
        {purchasedItems.length > 0 ? (
          <div className="purchased-grid">
            {purchasedItems.map((item, index) => (
              <div
                key={index}
                className="purchased-item-card"
              >
                <div id="image-box" onClick={() => handleItemClick(item.id)}>
                  <img src={`${API_URL}/${item.imageUrl}`} alt=" " />
                </div>
                <div className="purchased-item-details">
                  <p className="purchased-item-name">{item.name}</p>
                  <p className="purchased-item-price">{item.price}</p>
                  <p className="purchased-item-seller">
                    <img src="/images/icons/avatar.png" alt=" " className="seller-icon" />
                    {item.seller}
                  </p>
                  <p className="purchased-item-date">
                    : {dayjs(item.purchaseDate).format('YYYY MM DD')}
                  </p>
                  <button
                    className="review-button"
                    onClick={() => handleReviewClick(item)}
                  >
                     
                  </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <p>  .</p>
        )}
      </section>

      <Modal
        title=" "
        visible={isReviewModalVisible}
        onCancel={() => setIsReviewModalVisible(false)}
        footer={null}
      >
        <Form form={form} onFinish={handleReviewSubmit}>
          <Form.Item name="rating" label="" rules={[{ required: true, message: ' ' }]}>
            <Rate />
          </Form.Item>
          <Form.Item name="comment" label="" rules={[{ required: true, message: ' ' }]}>
            <Input.TextArea rows={4} />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
               
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}

export default MyPage;
